"use client"

import { createContext, useContext, useState, useEffect, type ReactNode } from "react"
import { cartApi } from "./api"
import type { CartItem, Product } from "./types"
import { useAuth } from "./auth-context"

interface CartContextType {
  items: CartItem[]
  addItem: (product: Product, quantity: number, color: string) => Promise<void>
  removeItem: (productId: string, color: string) => Promise<void>
  updateQuantity: (productId: string, color: string, quantity: number) => Promise<void>
  clearCart: () => Promise<void>
  refreshCart: () => Promise<void>
  total: number
  itemCount: number
  isLoading: boolean
}

const CartContext = createContext<CartContextType | undefined>(undefined)

export function CartProvider({ children }: { children: ReactNode }) {
  const [items, setItems] = useState<CartItem[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const { isAuthenticated } = useAuth()

  const refreshCart = async () => {
    if (!isAuthenticated) {
      setItems([])
      setIsLoading(false)
      return
    }

    try {
      setIsLoading(true)
      const response = await cartApi.get()
      setItems(response.cart.items || [])
    } catch (error) {
      console.error("Failed to fetch cart:", error)
      setItems([])
    } finally {
      setIsLoading(false)
    }
  }

  useEffect(() => {
    refreshCart()
  }, [isAuthenticated])

  const addItem = async (product: Product, quantity: number, color: string) => {
    if (!isAuthenticated) {
      throw new Error("Vui lòng đăng nhập để thêm sản phẩm vào giỏ hàng")
    }

    try {
      await cartApi.addItem(product._id, quantity, color)
      await refreshCart()
    } catch (error) {
      const message = error instanceof Error ? error.message : "Không thể thêm sản phẩm vào giỏ hàng"
      throw new Error(message)
    }
  }

  const removeItem = async (productId: string, color: string) => {
    if (!isAuthenticated) {
      throw new Error("Vui lòng đăng nhập")
    }

    try {
      await cartApi.removeItem(productId, color)
      await refreshCart()
    } catch (error) {
      const message = error instanceof Error ? error.message : "Không thể xóa sản phẩm"
      throw new Error(message)
    }
  }

  const updateQuantity = async (productId: string, color: string, quantity: number) => {
    if (!isAuthenticated) {
      throw new Error("Vui lòng đăng nhập")
    }

    if (quantity <= 0) {
      await removeItem(productId, color)
      return
    }

    try {
      await cartApi.updateItem(productId, color, quantity)
      await refreshCart()
    } catch (error) {
      const message = error instanceof Error ? error.message : "Không thể cập nhật số lượng"
      throw new Error(message)
    }
  }

  const clearCart = async () => {
    if (!isAuthenticated) {
      setItems([])
      return
    }

    try {
      await cartApi.clear()
      await refreshCart()
    } catch (error) {
      console.error("Failed to clear cart:", error)
    }
  }

  const total = items.reduce((sum, item) => sum + item.price * item.quantity, 0)
  const itemCount = items.reduce((sum, item) => sum + item.quantity, 0)

  return (
    <CartContext.Provider
      value={{
        items,
        addItem,
        removeItem,
        updateQuantity,
        clearCart,
        refreshCart,
        total,
        itemCount,
        isLoading,
      }}
    >
      {children}
    </CartContext.Provider>
  )
}

export function useCart() {
  const context = useContext(CartContext)
  if (!context) {
    throw new Error("useCart must be used within a CartProvider")
  }
  return context
}
